  
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { DashboardComponent } from './components/dashboard/dashboard.component';
import { VistaGeneralComponent } from './components/vista-general/vista-general.component';
import { TableroComponent } from './components/tablero/tablero.component';
import { ClientesComponent } from './components/clientes/clientes.component';
import { CaracteristicasComponent } from './components/caracteristicas/caracteristicas.component';
import { AgregarLocalComponent } from './components/agregar-local/agregar-local.component';

const routes: Routes = [
    {
        path: 'dashboard',
        component: DashboardComponent,
        children: [
            {
                path: 'vistaGeneral',
                component: VistaGeneralComponent
            },
            {
                path: 'tablero',
                component: TableroComponent
            },
            {
                path:'clientes',
                component:ClientesComponent
            },
            {
                path:'caracteristicas',
                component:CaracteristicasComponent
            },
            {
                path:'agregarLocal',
                component:AgregarLocalComponent
            }
        ]
    }
];


@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})
export class BodegueroRoutingModule { }