import { Component, OnInit, Input } from '@angular/core';

@Component({
  selector: 'app-almacen-item',
  templateUrl: './almacen-item.component.html',
  styleUrls: ['./almacen-item.component.scss']
})
export class AlmacenItemComponent implements OnInit {
  @Input() almacen
  unidades;
  imagenUrl;
  constructor() {
  }

  ngOnInit() {
    this.unidades=this.almacen.t_unidades
    this.imagenUrl=this.almacen.t_galeria[0].url
  }

}
