import { Component, OnInit, ViewChild } from '@angular/core';
import { conexion } from '../../../../global';
import { DataTableDirective } from 'angular-datatables';
import { Subject } from 'rxjs';
import { Router } from '@angular/router';

@Component({
  selector: 'app-vista-general',
  templateUrl: './vista-general.component.html',
  styleUrls: ['./vista-general.component.scss']
})
export class VistaGeneralComponent implements OnInit {
  @ViewChild(DataTableDirective, { static: false })
  dtElement: DataTableDirective;
  dtTrigger: Subject<any> = new Subject();
  dtOptions: DataTables.Settings = {};
  conexion: string = conexion
  id = 5
  miniBodega = {
    nombreOperador: ''
  }
  instalaciones = []

  constructor(private router: Router) { }

  ngOnInit() {
    this.dtOptions = {
      "language": {
        "url": "https://cdn.datatables.net/plug-ins/1.10.20/i18n/Spanish.json"
      },
    }
    console.log(`${this.conexion}/api/miniBodegas/getOne/${this.id}`)
    fetch(`${this.conexion}/api/miniBodegas/getOne/${this.id}`)
      .then(response => {
        return response.json()
      }).then(respuesta => {
        this.miniBodega = respuesta.content
        this.instalaciones = respuesta.content.t_instalaciones
        console.log(this.instalaciones)
        this.rerender()
      })
  }
  ngAfterViewInit(): void {
    this.dtTrigger.next();
  }
  ngOnDestroy(): void {
    // Do not forget to unsubscribe the event
    this.dtTrigger.unsubscribe();
  }
  rerender(): void {
    this.dtElement.dtInstance.then((dtInstance: DataTables.Api) => {
      // Destroy the table first
      dtInstance.destroy();
      // Call the dtTrigger to rerender again
      this.dtTrigger.next();
    });
  }
  agregarLocal(id) {
    this.router.navigateByUrl('/dashboard/agregarLocal')
    console.log(id)
  }
}
